import React, {Component} from 'react';
import stepData from './stepdata';
import StepCard from './StepCard';
import InteractiveSVG from './InteractiveSVG';
import $ from 'jquery';
import interact from 'interactjs';

class App extends Component {
   constructor() {
      super();
      this.state = {currentButton : 2, mode : 'learn', currentAnswerMark : 1, mediaDimTag : '', cardLocs : []};
      this.childClick = this.childClick.bind(this);
      this.startOver = this.startOver.bind(this);
      this.reset = this.reset.bind(this);
      this.upOne = this.upOne.bind(this);
      this.testMode = this.testMode.bind(this);
      this.learnMode = this.learnMode.bind(this);
      this.correctAnswer = this.correctAnswer.bind(this);
   }
   updateMedia() {
     let wdt = $('#media-container').width();
     let hgt = $('#media-container').height();
     let mediatag;
     if (wdt / hgt <= 1.2) mediatag = '_square';
     else mediatag = '';
     this.setState({currentButton : this.state.currentButton, mode : this.state.mode, currentAnswerMark : this.state.currentAnswerMark, mediaDimTag : mediatag});
   }
   componentDidMount() {
      this.updateMedia();
      window.addEventListener('resize', this.updateMedia.bind(this));
   }
   childClick() {
      this.setState({currentButton : this.state.currentButton + 1, mode : this.state.mode, currentAnswerMark : this.state.currentAnswerMark, mediaDimTag : this.state.mediaDimTag});
   }
   correctAnswer() {
      this.setState({currentButton : this.state.currentButton, mode : this.state.mode, currentAnswerMark : this.state.currentAnswerMark + 1, mediaDimTag : this.state.mediaDimTag});
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentAnswerMark}`).offsetTop;
      stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });
   }
   startOver() {
      this.setState({currentButton : 2, mode : this.state.mode, currentAnswerMark : this.state.currentAnswerMark, mediaDimTag : this.state.mediaDimTag});
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   reset() {
      this.setState({currentButton : 1, mode : this.state.mode, currentAnswerMark : this.state.currentAnswerMark, mediaDimTag : this.state.mediaDimTag});
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   upOne(evt) {
      evt.stopPropagation();
      this.setState({currentButton : this.state.currentButton - 1, mode : this.state.mode, currentAnswerMark : this.state.currentAnswerMark, mediaDimTag : this.state.mediaDimTag});
   }
   testMode() {
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1, mediaDimTag : this.state.mediaDimTag});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   learnMode() {
      this.setState({currentButton : 2, mode : 'learn', currentAnswerMark : 1, mediaDimTag : this.state.mediaDimTag});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      interact('#dragBut1').unset();
      interact('#dragBut2').unset();
   }
   render() {
      let modeMedia, butStyle, buttonChoices, mediaStyle;
      let stepCards = [];
      this.state.mode === 'learn' ? mediaStyle = {height : '54vh'} : mediaStyle = {minHeight : '54vh'};
      if (this.state.mode === 'learn') {
      buttonChoices = (<span><button onClick={this.startOver} className='uk-button uk-button-default uk-button-small uk-width-1-3' style={butStyle}>restart</button><button onClick={this.reset} className='uk-button uk-button-secondary uk-button-small uk-width-1-3'>clear</button><button onClick={this.testMode} className='uk-button uk-button-danger uk-button-small uk-width-1-3'>test</button></span>);
      for (let a = 0; a < stepData.length; a++) {
         stepCards.push(<StepCard key={stepData[a].id} label={stepData[a].label} text={stepData[a].text} order={stepData[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} upone={this.upOne} />)
      }
      if (this.state.currentButton <= stepData.length) butStyle = {pointerEvents : 'none', opacity : 0.5};
      else butStyle = {pointerEvents : 'all', opacity : 1};

      modeMedia = <img src={this.state.currentButton !== 1 ? `sbs_func${String(this.state.currentButton - 1)}${this.state.mediaDimTag}.svg` : ''} alt='' />;
      }
      else  {
      buttonChoices = (<span><button onClick={this.learnMode} className='uk-button uk-button-primary uk-button-small uk-width-1-1'>learn</button></span>);
      for (let a = 0; a < stepData.length; a++) {
         stepCards.push(<StepCard key={stepData[a].id} label={stepData[a].testLabel} text={stepData[a].test} order={stepData[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} curmark={this.state.currentAnswerMark} />)
      }
      if (this.state.currentButton <= stepData.length) butStyle = {pointerEvents : 'none', opacity : 0.5};
      else butStyle = {pointerEvents : 'all', opacity : 1};
      modeMedia = <InteractiveSVG curbut={this.state.currentButton} correctans={this.correctAnswer} />;
      }
      return (
      <div>
      <div id='media-container' className='uk-container uk-width-1-2@m uk-border-rounded' style={mediaStyle}>
      {modeMedia}
      </div>

      <div id='steps-container' className='uk-container uk-width-1-2@m'>
      {stepCards}
      {buttonChoices}
      </div>
      </div>
      );
   }
}

export default App;
