import React, {Component} from 'react';
import * as d3 from 'd3';
import interact from 'interactjs';
import $ from 'jquery';

class InteractiveSVG extends Component {
   constructor() {
      super();
      this.state = {part1clicks : 0, part1pnts : []};
      this.clickPart1 = this.clickPart1.bind(this);
      this.handleAnswer1 = this.handleAnswer1.bind(this);
      this.handleAnswer2 = this.handleAnswer2.bind(this);
      this.handleAnswer3 = this.handleAnswer3.bind(this);
      this.handleAnswer4 = this.handleAnswer4.bind(this);
   }
   componentDidMount() {
      this.drawSVG();
   }
   clickPart1(ref) {
      let pnts = this.state.part1pnts;
      let pntFill = d3.select('#circle' + ref.id).style('fill');
      if (this.state.part1clicks < 2 && pntFill === 'black') {
         d3.select('#circle' + ref.id).style('fill', 'green');
         pnts.push(ref.id);
         this.setState({part1clicks : this.state.part1clicks + 1, part1pnts : pnts});
      }
      else if (this.state.part1clicks < 2 && pntFill === 'green') {
         d3.select('#circle' + ref.id).style('fill', 'black');
         let point = pnts.indexOf(ref.id);
         pnts.splice(point, 1);
         this.setState({part1clicks : this.state.part1clicks - 1, part1pnts : pnts});
      }
      if (this.props.curbut === 2 && this.state.part1clicks === 2) {
         d3.selectAll('.clckpnt').style('pointer-events', 'none');
         this.handleAnswer1();
      }
   }
   dragFXLine(evt) {
      d3.select('#dragBut1').style('opacity', 0.2);
      var fxline = document.getElementById('fxLine');
      var target = evt.target;
      var y = (parseFloat(target.getAttribute('data-y')) || 0) + evt.dy;
      var y_fx = (parseFloat(fxline.getAttribute('data-y')) || 0) + evt.dy;
      target.style.webkitTransform = target.style.transform = 'translate(0px, ' + y + 'px)';
      fxline.style.webkitTransform = fxline.style.transform = 'translate(0px, ' + y_fx + 'px)';
      target.setAttribute('data-y', y);
      fxline.setAttribute('data-y', y_fx);
      d3.select('#trackLine1').attr('y2', Number(d3.select('#trackLine1').attr('y2')) + evt.dy);
      d3.select('#trackLine2').attr('y2', Number(d3.select('#trackLine2').attr('y2')) + evt.dy);
   }
   dragCircle(evt) {
      d3.select('#dragBut2').style('opacity', 0.2);
      var drgcrc = document.getElementById('dragCircle');
      var target = evt.target;
      var y = (parseFloat(target.getAttribute('data-y')) || 0) + evt.dy;
      var y_circ = (parseFloat(drgcrc.getAttribute('data-y')) || 0) + evt.dy;
      target.style.webkitTransform = target.style.transform = 'translate(0px, ' + y + 'px)';
      drgcrc.style.webkitTransform = drgcrc.style.transform = 'translate(0px, ' + y_circ + 'px)';
      target.setAttribute('data-y', y);
      drgcrc.setAttribute('data-y', y_circ);
      d3.select('#trackLine3').attr('y2', Number(d3.select('#trackLine3').attr('y2')) + evt.dy);
   }
   handleAnswer4() {
      let xcoord = $('#x-coord').val();
      let ycoord = $('#y-coord').val();
      if (xcoord === 'none' || ycoord === 'none') return;
      if (xcoord === '-3' && ycoord === '-6') {
         this.props.correctans();
         d3.select('.coord-pair').remove();
         d3.select('#coordText').transition().duration(1000).style('opacity', 1);
      }
      else {
        d3.selectAll('.coord-input').style('border', '1px solid red');
      }
   }
   handleAnswer3() {
      let circPos = Number(d3.select('#dragCircle').attr('data-y'));
      d3.select('#dragBut2').style('opacity', 0.8);
      if (circPos >= 58 && circPos <= 62) {
         d3.select('#dragBut2').remove();
         this.props.correctans();
         d3.select('.coord-pair').transition().duration(1000).style('opacity', 1);
         $('#x-coord').focus();
         window.addEventListener('resize', function() {
         })
      }
   }
   handleAnswer2() {
      let fxPos = Number(d3.select('#fxLine').attr('data-y'));
      d3.select('#dragBut1').style('opacity', 0.8);
      if (fxPos >= 58 && fxPos <= 62) {
         d3.select('#dragBut1').remove();
         d3.select('#fxLine').style('stroke', '#a22aff').style('stroke-dasharray', '5 10');
         let pntslen = (d3.selectAll('.clckpnt')._groups[0]).length;
         d3.select('#mxText').transition().delay(pntslen * 200).duration(1000).style('opacity', 1);
         d3.selectAll('.clckpnt')
           .transition().delay(function(d, i){return i * 200;}).duration(500).attr('r', 20)
           .style('opacity', 0).style('fill', 'none').style('stroke', 'red');
         this.props.correctans();
         setTimeout(function(){d3.selectAll('.clckpnt').remove();}, pntslen * 200);
         d3.select('#interactive').select('svg').append('line').attr('id', 'trackLine3')
           .attr('x1', 260).attr('y1', 260).attr('x2', 260).attr('y2', 260)
           .style('stroke', 'red').style('stroke-width', 1.5);
         d3.select('#interactive').select('svg').append('circle')
           .attr('cx', 260).attr('cy', 260).attr('r', 9).style('fill', 'green').style('opacity', 0)
           .transition().delay(pntslen * 200).duration(1000).style('opacity', 0.5);
         d3.select('#interactive').select('svg').append('circle').attr('id', 'dragCircle')
           .attr('cx', 260).attr('cy', 260).attr('r', 9).style('fill', 'green').style('opacity', 0);
         d3.select('#interactive').select('svg').append('text').attr('id', 'dragPointText')
           .attr('x', 170).attr('y', 250).style('fill', 'green').style('opacity', 0).text('(–3, –3)');
         d3.select('#dragCircle').transition().delay(pntslen * 200+500).duration(1000).style('opacity', 1);
         d3.select('#dragPointText').transition().delay(pntslen * 200+500).duration(1000).style('opacity', 1);
         d3.select('#interactive').select('svg').append('svg:image').attr('id', 'dragBut2')
           .attr('xlink:href', 'dragbutton.png').attr('width', 40).attr('height', 40)
           .attr('x', 245).attr('y', 270).style('opacity', 0).style('cursor', 'pointer')
           .transition().delay(2800).duration(500).style('opacity', 0.8);
         interact('#dragBut2').draggable({
            origin : 'parent',
            axis : 'y',
            onmove : this.dragCircle,
            onend : this.handleAnswer3
         });
      }
   }
   handleAnswer1() {
      if (this.state.part1pnts.indexOf('3') !== -1 && this.state.part1pnts.indexOf('4') !== -1) {
         this.props.correctans();
         d3.select('#interactive').select('svg').append('line').attr('id', 'trackLine1')
           .attr('x1', 400).attr('y1', 120).attr('x2', 400).attr('y2', 120)
           .style('stroke', 'red').style('stroke-width', 1.5);
         d3.select('#interactive').select('svg').append('line').attr('id', 'trackLine2')
           .attr('x1', 240).attr('y1', 280).attr('x2', 240).attr('y2', 280)
           .style('stroke', 'red').style('stroke-width', 1.5);
         d3.select('#interactive').select('svg').append('line')
           .attr('x1', -120).attr('y1', 640).attr('x2', -120).attr('y2', 640)
           .style('stroke', 'blue').style('stroke-width', 5).style('stroke-linecap', 'round')
           .transition().duration(1000).ease(d3.easeQuadInOut).attr('x2', 760).attr('y2', -240);
         d3.select('#interactive').select('svg').append('line').attr('id', 'fxLine')
           .attr('x1', -120).attr('y1', 640).attr('x2', -120).attr('y2', 640)
           .style('stroke', 'blue').style('stroke-width', 5).style('stroke-linecap', 'round')
           .transition().duration(1000).ease(d3.easeQuadInOut).attr('x2', 760).attr('y2', -240);
         d3.select('#fxText').transition().delay(800).duration(1000).style('opacity', 1);
         d3.select('#interactive').select('svg').append('svg:image').attr('id', 'dragBut1')
           .attr('xlink:href', 'dragbutton.png').attr('width', 40).attr('height', 40)
           .attr('x', 380).attr('y', 120).style('opacity', 0).style('cursor', 'pointer')
           .transition().delay(1000).duration(500).style('opacity', 0.8);
         interact('#dragBut1').draggable({
            origin : 'parent',
            axis : 'y',
            onmove : this.dragFXLine,
            onend : this.handleAnswer2
         });
      }
      else {
         let test1 = (this.state.part1pnts[0] !== '3' && this.state.part1pnts[0] !== '4');
         let test2 = (this.state.part1pnts[1] !== '3' && this.state.part1pnts[1] !== '4');
         let pnt1 = d3.select(`#circle${this.state.part1pnts[0]}`);
         let pnt2 = d3.select(`#circle${this.state.part1pnts[1]}`);
         setTimeout(function(){
         if (test1) {
         pnt1.transition().duration(500).attr('r', 20)
             .style('opacity', 0).style('fill', 'none').style('stroke', 'red');
         setTimeout(function(){pnt1.remove();}, 750);
         }
         if (test2) {
         pnt2.transition().delay(200).duration(500).attr('r', 20)
             .style('opacity', 0).style('fill', 'none').style('stroke', 'red');
         setTimeout(function(){pnt2.remove();}, 750);
         }
         d3.selectAll('.clckpnt').style('pointer-events', 'all').style('fill', 'black');
         }, 500);
         this.setState({part1clicks : 0, part1pnts : []});
      }
   }
   drawSVG() {
    const circData = [{x : 40, y : 100, id : '1'}, {x : 320, y : 340, id : '2'}, {x : 260, y : 260, id : '3'},
                      {x : 360, y : 160, id : '4'}, {x : 500, y : 200, id : '5'}, {x : 500, y : 80, id : '6'},
                      {x : 600, y : 360, id : '7'}, {x : 120, y : 340, id : '8'}, {x : 240, y : 120, id : '9'},
                      {x : 400, y : 280, id : '10'}];
      let wdt = 644;
      let hgt = 393.5;
      const svg = d3.select('#interactive').append('svg').attr('width', wdt).attr('height', hgt)
                    .attr('viewBox', '0 0 644 393.5').attr('preserveAspectRatio', 'xMidYMid');
      for (var a = 20; a < wdt - 20; a += 20) {
      svg.append('line').attr('x1', a).attr('y1', 0).attr('x2', a).attr('y2', hgt)
         .style('stroke', '#36a9e1').style('stroke-width', 0.9);
      }
      for (var b = 20; b < hgt; b += 20) {
      svg.append('line').attr('x1', 0).attr('y1', b).attr('x2', wdt).attr('y2', b)
         .style('stroke', '#36a9e1').style('stroke-width', 0.9);
      }
      svg.append('line').attr('x1', 320).attr('y1', 0).attr('x2', 320).attr('y2', hgt)
         .style('stroke', 'black').style('stroke-width', 2);
      svg.append('line').attr('x1', 0).attr('y1', 200).attr('x2', wdt).attr('y2', 200)
         .style('stroke', 'black').style('stroke-width', 2);
      svg.selectAll('circle')
         .data(circData)
         .enter()
         .append('circle')
         .attr('class', 'clckpnt')
         .attr('id', function(d){return `circle${d.id}`;})
         .attr('cx', function(d){return d.x;})
         .attr('cy', function(d){return d.y;})
         .attr('r', 9).style('fill', 'black')
         .on('click', this.clickPart1);
      svg.append('text').attr('id', 'fxText').attr('x', 400).attr('y', 25).text('f(x) = x')
         .style('fill', 'blue').style('stroke', 'none').style('opacity', 0);
      svg.append('text').attr('id', 'mxText').attr('x', 550).attr('y', 55).text('m(x)')
         .style('fill', '#a22aff').style('stroke', 'none').style('opacity', 0);
      svg.append('text').attr('id', 'coordText').attr('x', 280).attr('y', 330).text('(–3, –6)')
         .style('fill', 'green').style('stroke', 'none').style('opacity', 0);
   }
   render() {
      return (
      <div>
      <div className='coord-pair'>
      <span style={{fontSize : '1.4em'}}>(&thinsp;</span>
      <select name='ddown1' onChange={this.handleAnswer4} className='coord-input' id='x-coord' value='-3'>
      <option value="none"></option>
      <option value="-6">&ndash;6</option>
      <option value="-5">&ndash;5</option>
      <option value="-4">&ndash;4</option>
      <option value="-3">&ndash;3</option>
      <option value="-2">&ndash;2</option>
      <option value="-1">&ndash;1</option>
      </select>&nbsp;,&nbsp;
      <select name='ddown2' onChange={this.handleAnswer4} className='coord-input' id='y-coord' value='-3'>
      <option value="none"></option>
      <option value="-6">&ndash;6</option>
      <option value="-5">&ndash;5</option>
      <option value="-4">&ndash;4</option>
      <option value="-3">&ndash;3</option>
      <option value="-2">&ndash;2</option>
      <option value="-1">&ndash;1</option>
      </select>
      <span style={{fontSize : '1.4em'}}>&thinsp;)</span>
      </div>
      <div id='interactive'></div>
      </div>
      );
   }
}

export default InteractiveSVG;
