import React, {Component} from 'react';
import $ from 'jquery';

class StepCard extends Component {
   constructor() {
      super();
      this.state = {};
   }
   componentDidMount() {
      $(`#steptext${this.props.order}`).html(this.props.text);
   }
   render() {
      let cardStyle, labelStyle, arrowStyle;
      if (this.props.curmode === 'learn') {
        if (this.props.order < this.props.curbut) {
        cardStyle = {pointerEvents : 'none', opacity : 1};
        labelStyle = {backgroundColor : '#1e87f0'};
        }
        else if (this.props.order === this.props.curbut) {
        cardStyle = {pointerEvents : 'all', opacity : 0.5};
        labelStyle = {backgroundColor : 'gray'};
        }
        else {
        cardStyle = {pointerEvents : 'none', opacity : 0.5};
        labelStyle = {backgroundColor : 'gray'};
        }
        if (this.props.order === this.props.curbut-1 && this.props.curbut > 1) {
           arrowStyle = {visibility : 'visible', cursor : 'pointer', pointerEvents : 'all'}
        }
        else {
           arrowStyle = {visibility : 'hidden', cursor : 'none', pointerEvents : 'none'}
        }
      }
      else {
        arrowStyle = {visibility : 'hidden', cursor : 'none', pointerEvents : 'none'}
        if (this.props.order > this.props.curmark) {
        cardStyle = {pointerEvents : 'none', opacity : 0.5};
        labelStyle = {backgroundColor : 'gray'};
        }
        else {
        cardStyle = {pointerEvents : 'none', opacity : 1};
        labelStyle = {backgroundColor : '#1e87f0'};
        }
      }
      return (
      <div className='uk-card uk-card-default uk-card-body uk-card-small uk-width-1-1@m uk-margin-small-bottom' id={`card${this.props.order}`} onClick={this.props.handler} style={cardStyle}>
      <div><span className='arrow' onClick={this.props.upone} style={arrowStyle}>↑</span><span className="uk-label" style={labelStyle}>{this.props.label}</span> <p className='step-text' id={`steptext${this.props.order}`}>{this.props.text}</p></div>
      </div>
      );
   }
}

export default StepCard;
